@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
:root{
  --sidebar-flex:1.5;
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.6rem;
  --typing-font:3rem;
}
*
{
  margin:0;
  padding: 0;
}
body{
  font-family: 'Times New Roman';
}
.App
{
  display: -webkit-flex;
  display: flex;
  width: 100vw;
  min-height: 100vh;
}
nav
{
  padding:60px 30px 0px 30px ;
  -webkit-flex:1.5;
          flex:1.5;
  -webkit-flex:var(--sidebar-flex);
          flex:var(--sidebar-flex);
  background: rgb(238,103,143);
  background: linear-gradient(180deg, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%);
}
.toggle-button {
    position: absolute;
    top: .75rem;
    right: 1rem;
    display: none;
    -webkit-flex-direction: none;
            flex-direction: none;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 30px;
    height: 21px;
}
.toggle-button .bar {
  height: 3px;
  width: 100px;
  border-radius: 10px;
}
.logo
{
  border-radius: 500%;
  width: 200px;
  margin: 30px;
  border: 2px solid powderblue;
}
.condiv
{
  padding: 100px ;
  background-color: #f4f5f9;
  -webkit-flex:6;
          flex:6;
  -webkit-flex:var(--condiv-flex);
          flex:var(--condiv-flex);
}

.home
{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  font-family: Cursive;
}
.typingeffect
{
  font-size: 3rem;
  font-size: var(--typing-font);
  margin-bottom: 30px;
}

nav ul{
  font-size:1.6rem;
  font-size:var(--fontsize-nav) ;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: white !important;
}
nav ul li
{
  margin:40px;
}

a
{
  text-decoration: none;
  color: white;
}

.subtopic
{
  color: blue;
  margin:10px 10px 30px 10px !important;
  text-decoration: underline;
}
p{
  font-size: 1.2rem;
  font-size: var(--fontsize-p);
  margin: 10px;
}
h1,h2,h3,h4{
  margin: 10px;
}
.social
{
  position: absolute;
  margin-top: 20px;
  display: -webkit-flex;
  display: flex;
  bottom: 60px;
}
.social i
{
  padding-right: 30px;
  font-size: 25px;
  color: #9a9b9c;
}
.social i:hover
{
  color: #ffffff;
}
.widecard
{
  border:0px solid #9a9b9c;
  display: -webkit-flex;
  display: flex;
  margin:30px 0px 30px 0px;
}
.widecard:hover
{
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.active
{
  font-weight: bolder;
}
.skills ul li
{
  list-style-type:none;
  margin:30px;
}

@media screen and (max-width: 800px) {
  :root {
    --fontsize-nav:1rem;
    --sidebar-flex:0;
    --typing-font:2rem;
  }
  .App
  {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .sidebar
  {
    padding: 30px;
    background: rgb(238,103,143);
    background: linear-gradient(90deg, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%);
  }
  .condiv
  {
    padding: 50px ;
  }
  nav ul
  {
    display: -webkit-flex;
    display: flex;
  }
  nav ul li
  {
    margin: 10px;
  }
  .social
  {
    left:0;
    width: 100vw;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    bottom: 30px;
  }
  .social i
  {
    padding-right: 0px;
  }
}
@media screen  and (min-width: 500px) {
  nav ul
  {
    display: top;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
 }
 .condiv{
   
 }
}
